// ========================================================================================
// * Lib
import React, { useState, useRef, useEffect } from 'react'

// * Utils
import { gifs } from '@constants'
// * Styles
import './styles.scss'

// * Display/UI
import { Envelope, Button, Confetti } from '@atoms'
import { Video } from '@modules'
import videoSrc from './butterfly.webm'

interface IState {
   throwConfetti: boolean
}

const LoveScreen = () => {
   const [state, setState] = useState({
      throwConfetti: false,
      videoReady: false,
   })

   const envelopeRef = useRef(null)
   const videoRef = useRef(null)
   useEffect(() => {
      if (videoRef.current) {
         videoRef.current.addEventListener(
            'loadeddata',
            () => setState({ ...state, videoReady: true }),
            false,
         )
      }
   }, [videoRef])

   const onHeartButtonClick = () => {
      const isFocused = envelopeRef.current?.getAttribute('data-focused')
      envelopeRef.current?.setAttribute(
         'data-focused',
         isFocused == 'false' ? 'true' : 'false',
      )
      setState({ ...state, throwConfetti: true })
   }
   return (
      <div className="love">
         <Confetti throwConfetti={state.throwConfetti} />
         <Video src={videoSrc} videoRef={videoRef} />
         {state.videoReady && (
            <>
               <div className="love__envelop-container">
                  <Envelope
                     ref={envelopeRef}
                     message="Te Amo"
                     destinatary="Para: Mi Pretz"
                  >
                     <div className="heart" />
                     {state.throwConfetti && (
                        <img
                           className="love__balloons-couple"
                           src={gifs.BALLOON_COUPLE}
                        />
                     )}
                  </Envelope>
               </div>
               <div className="love__heart-button">
                  <Button onPress={onHeartButtonClick} />
               </div>
               <img
                  className="love__nori-worried"
                  src={gifs.NORI_WORRIED}
                  alt=""
               />
            </>
         )}
      </div>
   )
}

export default LoveScreen
